@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}

:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --white: #ffffff;
  --black: #000000;
  --black-light: #111111;
  --green: #68b738;
  --grey-dark-special: #222222;
  --grey-dark: #444444;
  --grey-special: #666666;
  --grey-basic_background: #f2f2f2;
  --grey_background: #eaeaea;
  --green_background: rgba(104, 183, 56, 0.85);
  --transparent: rgba(0, 0, 0, 0);
}

img {
  max-width: 100%;
  object-fit: contain;
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--main-font);
}

.page__body {
  margin: 0;
  min-height: 100%;
  background-color: var(--white);
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

// надо что то написать

.button {
  font-family: var(--accent-font);
  font-weight: 400;
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--green);
  border: none;

  &--size_wide {
    width: 100%;
  }
}

.page-header {
  padding: 10px 20px 17px 20px;
}

.page-header__logo-image {
  margin-right: 57px;
}

.page-header__logo-link {
  text-decoration: none;
}

.page-footer__contacts {
  padding: 20px;
}

.page-footer__logo {
  padding: 20px;
  background-color: var(--grey-basic_background);
}

.page-footer__social {
  padding: 20px;
  background-color: var(--grey-basic_background);
}

.page-footer__copyright {
  font-size: 16px;
  line-height: 18px;
  padding: 20px;
  background-color: var(--grey-basic_background);
}

.page-footer__iframe {
  border: none;
}

.contacts__cooperation {
  font-style: normal;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black-light);
  text-transform: uppercase;
}

.contacts__text {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-dark);
}

.copyright__link {
  color: var(--grey-dark);
  text-decoration: none;
}

.main-nav {
  font-family: var(--accent-font);
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-nav__link {
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
}

.selection {
  font-family: var(--accent-font);
  color: var(--white);
  padding: 20px;
  background-image:
    linear-gradient(to bottom, var(--green_background), var(--green_background)),
    url("../img/index-background-cat.jpg");
  background-size: 320px 302px;
  background-repeat: no-repeat;
}

.selection__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 0;
}

.selection__slogan {
  font-style: normal;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  /*Временный фон, что бы не потерять белое на белом*/
  background-color: rgb(238, 130, 189);
}

.selection__button-link {
  font-size: 14px;
  line-height: 18px;
}

.advantages {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
  padding: 0 20px;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.advantages__item {
  background-color: var(--grey-basic_background);
  padding: 20px;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.advantages__title {
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  margin: 0;
}

.advantages__button-link {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 16px;
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
}

.technologies {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
  padding: 20px;
}

.technologies__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: var(--black);
  margin-top: 0;
  margin-bottom: 40px;
}

.technologies__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.technologies__item {
  margin-top: 0;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.compare {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
  padding: 20px;
  background-color: var(--grey_background);
}

.compare__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: var(--black);
  padding-top: 5px;
  margin-top: 0;
  margin-bottom: 40px;
}

.compare__effect-term {
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
}

.compare__effect-definition {
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
}

.compare__expenses-list {
  font-family: var(--accent-font);
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
}

.compare__range-button {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  border: none;
  background-color: var(--transparent);
}

.form-selection {
  font-family: var(--accent-font);
  font-weight: 400;
}

.form-selection__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: var(--black);
  padding-top: 27px;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 37px;
}

.form-selection__description {
  font-size: 14px;
  line-height: 18px;
  color: var(--black);
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.form-selection__attention {
  display: block;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: var(--black);
  padding: 0 20px;
  margin-bottom: 32px;
}

.form-selection__set {
  margin: 0;
}

.form__button {
  margin: 20px;
}

.form-cat {
  font-size: 16px;
  line-height: 18px;
  color: var(--grey-dark);
  text-transform: uppercase;
  border: none;
  padding: 20px;
}

.form-cat__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-cat__item {
  &:not(:last-child) {
    margin-bottom: 14px;
  }
}

.form-cat__field {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
}

.form-radio {
  font-size: 16px;
  line-height: 18px;
  color: var(--grey-dark-special);
  text-transform: uppercase;
  border: none;
  padding: 20px;
}

.form-radio__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-radio__item {
  &:not(:last-child) {
    margin-bottom: 13px;
  }
}

.form-owner {
  border: none;
  padding: 20px;
}

.form-owner__legend {
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
  padding: 0;
}

.form-owner__list {
  font-size: 16px;
  line-height: 18px;
  color: var(--grey-dark);
  text-transform: uppercase;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-owner__item {
  &:not(:last-child) {
    margin-bottom: 14px;
  }
}

.form-owner__field {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
}

.form-commentary {
  padding: 20px;
}

.form-commentary__label {
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
}

.form-commentary__field {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
}

.form-checkbox {
  border: none;
  padding: 20px;
}

.form-checkbox__legend {
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
  padding: 0;
}

.form-checkbox__list {
  font-size: 16px;
  line-height: 18px;
  color: var(--black);
  text-transform: uppercase;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-checkbox__item {
  &:not(:last-child) {
    margin-bottom: 12px;
  }
}

.catalog-main__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: var(--black);
  padding-top: 27px;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 40px;
}

.catalog__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.catalog__card {
  padding: 20px;
}

.product__link {
  text-decoration: none;
}

.product__title {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--grey-dark-special);
  text-transform: uppercase;
  margin: 0;
}

.product__title--more {
  color: var(--black-light);
  margin-bottom: 8px;
}

.product__specification {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--grey-dark);
  margin: 0;
}

.product__description--more {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--grey-dark);
  margin-top: 0;
  margin-bottom: 21px;
}

.product__definition {
  margin: 0;
}

.product__button {
  font-size: 16px;
  line-height: 20px;
}

.product__button--more_color {
  color: var(--grey-dark);
  background-color: var(--grey-basic_background);
}

.catalog-add__card {
  &:not(:last-child) {
    margin-bottom: 19px;
  }
}

.catalog-add__list {
  padding: 20px;
}

.catalog-add__title {
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: var(--black);
  padding-top: 20px;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 36px;
}

.catalog-add__aside {
  margin: 20px;
}

.product-add__title {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
}

.product-add__link {
  color: var(--grey-dark-special);
  text-decoration: none;
}

.product-add__quantity,
.product-add__price {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: var(--grey-dark);
  margin: 0;
}

.product-add__price {
  text-transform: uppercase;
}

.product-add__order {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  margin-top: 12px;
  margin-left: 0;
}

.catalog-aside {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--white);
  background-image:
    linear-gradient(to bottom, var(--green_background), var(--green_background)),
    url("../img/catalog-background-cat.jpg");
  background-size: 279px 289px;
  background-repeat: no-repeat;
}

.catalog-aside__description {
  padding-top: 187px;
  padding-bottom: 43px;
}
